.App {
  margin-top: 20px;
}

.gameMessage {
  font-size: 20px;
}

.gameCounter {
  font-size: 30px;
  opacity: 1;
  transition: opacity ease-in-out 4s;
  -webkit-transition: opacity ease-in-out 4s;
  -moz-transition: opacity ease-in-out 4s;
  -o-transition: opacity ease-in-out 4s;
  -ms-transition: opacity ease-in-out 4s;
}

.fade-out {
  opacity: 0;
}
